body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{margin:0;position:relative;min-height:100vh;font-family:"Montserrat";font-size:35px;background-color:#000;display:flex;flex-direction:column}

.FlexContainer{display:flex;justify-content:flex-end;justify-content:space-between;align-self:center;max-height:10vh;margin:0;background-color:#1d1d1d}.FlexContainer .items-container{display:flex;flex-direction:row;margin-right:2vh}.FlexContainer .logo{display:flex;align-self:center;margin:0}.FlexContainer .logolink{display:flex;align-self:center;margin-left:-2vw}.FlexContainer .pic{height:80%;width:40%}.FlexContainer .Nav-item{text-decoration:none;margin:0;padding:1rem;color:#fff;background-color:#1d1d1d}.FlexContainer .Nav-item:hover{color:#17fea0;padding:1rem;background-color:#101010;border-color:ligthen(#1d1d1d, 5%)}.FlexContainer .log-inn{display:flex;align-items:center;text-decoration:none;margin-top:1rem;margin-bottom:1rem;font-weight:bold;padding-right:1rem;padding-left:1rem;font-size:25px;color:#fff;background-color:#101010;border:2px outset #17fea0;border-radius:300px}.FlexContainer .log-inn:hover{color:#17fea0;border:2px solid #ff6680;margin-top:1rem;margin-bottom:1rem;padding-right:1rem;padding-left:1rem;background-color:#101010;border-radius:300px}

.Home{display:flex;flex-direction:column;align-items:center;font-family:"Montserrat";margin:0;padding:1em 0;margin-bottom:10px;color:#fff}.Home .H-el{text-align:center;margin-bottom:3%}.Home .H-el .pic{width:100%}

.About{display:flex;flex-direction:row;justify-content:center;text-align:center;font-family:"Montserrat";padding:1em 0;margin-bottom:10px;color:#fff}.About .cont{display:flex;flex-direction:column;margin:0 auto;width:50%}.About .cont .h1{color:#17fea0}.tekst{color:white;margin:0 auto;margin-bottom:10%;width:70%}

.Dashboard{text-align:center;color:#fff;height:100%}.Dashboard .Dash{margin-top:4vh;display:flex;flex-direction:row;min-height:100%;widows:100%}.Dashboard .Dash .navbar{margin:0}.Dashboard .Dash .screen{display:flex;flex-direction:row;justify-content:flex-start;align-items:flex-start;width:100%;min-height:100%;margin:0;margin-bottom:10%}.Dashboard .Dash .screen .Element{display:flex;flex-direction:column;text-align:center;background-color:#1d1d1d;border-radius:5px;max-height:25vw;min-height:25vw;min-width:25vw;max-width:25vw}.Dashboard .Dash .screen .Element .Maps{margin-left:10rem;width:100rem;height:100rem;border:3px solid blue}.Dashboard .signin{align-self:flex-start}.Dashboard h1{color:#fff}

.Flex-navbar{display:flex;flex-direction:column;align-content:flex-start;justify-content:flex-start;align-items:center;margin:0rem;padding:0;height:100%;margin-top:-2rem;margin-bottom:-2rem;color:#fff;background-color:#1d1d1d;font-size:1rem;width:8vw}.Flex-navbar .items-container{display:flex;flex-direction:column;align-content:space-between;justify-content:space-between;font-size:1.2rem;font-weight:bold;width:100%}.Flex-navbar .logo{display:flex;display:flex;align-self:center;margin:0}.Flex-navbar .logolink{margin:0;margin-top:1rem;margin-bottom:1rem;display:flex;flex-direction:column;align-content:center;justify-content:center;align-items:center;display:flex}.Flex-navbar .pic{width:70%;height:70%;border-radius:300px}.Flex-navbar .user{text-align:center;font-size:100%;margin:0}.Flex-navbar .Nav-item{text-decoration:none;text-align:center;margin:0;padding-top:10%;padding-bottom:10%;width:100%;font-size:130%;color:#fff;background-color:#1d1d1d;border-top:1px solid #101010}.Flex-navbar .Nav-item:hover{color:#17fea0;background-color:#101010;border-color:ligthen(#1d1d1d, 5%)}.signout{text-decoration:none;margin-top:1rem;margin-bottom:1rem;font-weight:bold;padding:0.5vw;font-size:1.5vw;color:#fff;background-color:#101010;border:2px outset #17fea0;border-radius:300rem}.signout:hover{color:#17fea0;border:2px outset #ff6680;margin-top:1rem;margin-bottom:1rem;background-color:#101010;border-radius:300px}

.Maps{display:flex;flex-direction:column;justify-content:flex-start;width:100%;min-height:50rem;margin:3px auto;position:"absolute"}

.Weather{min-height:90vh;padding:1em 0;width:70%;height:100%;color:white;text-align:center;max-width:50%}.Weather .margin{border-radius:5px;max-width:70%;margin:0 auto}p{color:#17fea0}button.search-button{color:#17fea0;font-weight:bold;background-color:#181818;text-decoration:none;padding:10px 20px;padding-bottom:15px;font-size:30px;border-radius:15px;border-color:#01e186;text-align:center;text-decoration:none}button.search-button:hover{border:2px outset #ff6680;border-radius:10px}input.search-bar{font-size:20px;background-color:#2a2a2a;border-radius:5px;padding:20px;color:white;font-weight:bold}input.search-bar:focus{outline:none;border:2px solid #17fea0}h3{color:red;font-weight:bold;font-size:2rem}.search-form{display:flex;justify-content:center;margin:0 auto}

.Default{justify-content:center;width:100%}.Default .Weather{margin:0 auto}

.Data{display:flex;flex-direction:column;margin:0 auto;justify-content:flex-start;width:100%;min-height:100vh;padding:0;margin:1%;color:#fff;margin-bottom:3%}.Data .Dato{display:flex;flex-direction:row;width:auto;height:10%;justify-content:center}.Data .Dato .datoBoks{display:flex;flex-direction:column;margin:1rem;width:auto;height:auto;text-align:left;font-display:rigth;font-size:1.2rem}.Data .Dato .datoBoks .fromTo{margin:0;width:auto}.Data .dataButton{text-decoration:none;margin-top:1rem;margin-bottom:1rem;font-weight:bold;border-radius:10px;padding:0.5vw;font-size:1.2vw;color:#fff;background-color:#101010;border:1px outset #17fea0}.Data .dataButton:hover{color:#17fea0;border:1px outset #ff6680;margin-top:1rem;margin-bottom:1rem;background-color:#101010;border-radius:300px}.Data .dataBar{width:10%;font-size:20px;background-color:#2a2a2a;border-radius:5px;padding:20px;margin:0;color:white;font-weight:bold}.Data .dataBar:focus{outline:none;border:2px solid #17fea0}p{color:white}.Bird{display:flex;justify-content:space-between}.counter{color:#17fea0}.dateForm{margin:0;width:auto;height:auto;font-size:20px;font-family:Montserrat;background-color:#2a2a2a;border-radius:5px;border:none;padding:0.4rem;padding-top:0.5rem;padding-bottom:0.5rem;color:#17fea0;font-weight:bold;border:1px outset #1d1d1d}.nodeSelect{margin-right:1rem;width:95%;height:auto;font-size:20px;font-family:Montserrat;background-color:#2a2a2a;border-radius:5px;border:none;padding:0.4rem;padding-top:0.5rem;padding-bottom:0.5rem;color:#17fea0;font-weight:bold;border:1px outset #1d1d1d}.Card{display:flex;justify-content:center;width:100%;height:auto;margin:0}

.FlexFooter{display:flex;justify-content:space-between;align-items:center;position:absolute;bottom:0px;align-self:center;width:100%;min-height:8vh;margin:0;background-color:#1d1d1d;margin-top:2vh}.FlexFooter .component{display:flex;justify-content:space-between;margin:1%;min-width:12%}.FlexFooter .component .F-items{display:flex;align-items:center;max-height:90vh}.FlexFooter .F-Logo{max-height:6vh;padding-right:3vh}.BirdLogo{text-decoration:none;color:#17fea0;font-weight:bold;font-size:50%;margin-right:1%;margin-left:1%;width:90%;text-align:center}

